.car-search{
  margin: 2.5em auto 0;
  width: 66em;
  max-width: 100%;
  display: none;
  &__bg{
    display: none;
  }
  &__img{
    width: 2.4375em;
    margin-right: 1.25em;
  }
  &__title{
    color: $blue;
  }
  &__grid{
    display: grid;
    grid-template-columns: 12.5em 12.5em 12.5em 12.5em 8.625em;
    justify-content: space-between;
    margin-top: 1.25em;
  }
  &__btn{
    width: 100%;
    height: 2.5em;
    padding: 0;
  }
  &__close{
    display: none;
  }
}

.select-block{
  width: 100%;
  .select2{
    width: 100% !important;
  }
  .select2-container .select2-selection--single .select2-selection__rendered{
    padding-left: .5em;
    padding-right: 2.5em;
  }
  .select2-selection--single{
    border-color: $blue;
    height: 2.5em;
    padding: 0 .25em;
    .select2-selection__rendered{
      font-size: 1em;
      line-height: 2.5em;
    }
    .select2-selection__placeholder{
      color: #929292;
    }
    .select2-selection__arrow{
      height: 100%;
      top: 0;
      right: .625em;
      @include center;
      b{
        border: none;
        width: 1em;
        height: .4375em;
        display: block;
        background-image: url(../img/select-arrow2.svg);
        @include bgImgSize(100%);
        margin: 0;
        position: static;
      }
    }
    .select2-selection__clear{
      width: .625em;
      height: .625em;
      top: 50%;
      left: unset;
      right: 1em;
      margin-right: 1.25em;
      transform: translateY(-50%);
      background-image: url(../img/clear.svg);
      @include bgImgSize(100%);
      position: absolute;
      span{
        display: none;
      }
    }
  }
}

.select2-dropdown{
  border-color: $blue;
  @media screen and (min-width: 993px){
    margin-top: -1px;
    width: 26.75em !important;
    border-top: 1px solid $blue !important;
    border-radius: 0 .3125em .3125em .3125em !important;
    &._select4{
      width: calc(12.5em + 1.75em + 8.625em) !important;
    }
  }
}

.select2-container--default .select2-search--dropdown .select2-search__field{
  border-color: #CCC9C9;
  height: 1.875em;
  border-radius: .3125em;
  font-size: 1em;
  background-image: url(../img/search-icon2.svg);
  background-size: 1em auto;
  background-position: right .44em center;
  background-repeat: no-repeat;
}

.select2-results__option{
  font-size: .75em;
  border-radius: .4em;
  display: flex;
  align-items: center;
}

.select2-container--default .select2-results>.select2-results__options{
  max-height: 32em;
  padding: 0 1em 1em;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}

.select2-search--dropdown{
  padding: 1em;
}

.select2-container--open{
  @media screen and (min-width: 993px){
    &:before{
      content: '';
      position: absolute;
      top: 100%;
      left: 0;
      width: 100%;
      height: .625em;
      background-color: $blue;
    }
    .select2-selection__placeholder{
      color: $white !important;
    }
    .select2-selection__rendered{
      padding: 0 1em !important;
      text-align: center;
      color: $white !important;
    }
    .select2-selection__clear,
    .select2-selection__arrow{
      opacity: 0;
      pointer-events: none;
    }
    .select2-selection--single{
      background-color: $blue;
    }
  }
}

.select2-results__option--highlighted.select2-results__option--selectable{
  background-color: $blue !important;
}

.select2-container{
  @media screen and (min-width: 993px){
    margin-top: .625em;
  }
}

.js-select2 ~ .select2-container{
  margin-top: 0;
}


@media screen and (max-width: 992px){
  .header{
    &._open-form{
      .car-search{
        opacity: 1;
        pointer-events: unset;
        &__bg{
          display: block;
        }
      }
    }
  }

  .car-search{
    transition: all .3s;
    opacity: 0;
    pointer-events: none;
    display: block !important;
    position: absolute;
    top: 4.5em;
    left: 0;
    width: 100%;
    background: #EBEBEB;
    margin: 0;
    padding: .875em 1.875em 2.875em;
    z-index: 10;
    &__bg{
      position: fixed;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      z-index: 9;
      opacity: 0;
    }
    &__img{
      width: 1.4375em;
      margin-right: .75em;
    }
    &__title{
      font-size: .875em;
    }
    &__grid{
      grid-template-columns: 100%;
      gap: .875em;
      margin-top: 1.25em;
    }
    &__btn{
      width: 8em;
      height: 2.3125em;
      display: inline-flex;
      margin: 0 auto;
    }
    &__close{
      @include center;
      width: 1.05em;
      margin-left: auto;
      img{
        width: 100%;
      }
    }
  }

  .select-block{
    font-size: .875em;
  }

  .select2-search--dropdown{
    padding: .75em;
  }

  .select2-container--default .select2-results>.select2-results__options{
    padding: 0 .5em .75em;
  }
}


