.contacts{
  padding-top: 6em;
  &__grid{
    display: grid;
    grid-template-columns: repeat(2,calc(50% - .9375em));
    gap: 1.875em;
  }
}

.contacts-form{
  padding: 1.75em 2em 2em;
  background-color: #F5F5F5;
  &__text{
    text-align: center;
    @include grid100gap(1.5em);
  }
  &__desc{
    font-size: 1.25em;
    line-height: 130%;
    font-weight: 300;
  }
  &__fields{
    margin-top: 2em;
    @include grid100gap(2em);
  }
  &__btn{
    margin-top: 3.75em;
    width: 16em;
    height: 2.75em;
    font-weight: 500;
    span{
      font-size: 1.5em;
    }
  }
}

.contacts-info{
  text-align: center;
  display: flex;
  flex-direction: column;
  align-items: center;
  &__text{
    font-size: 1.25em;
    line-height: 160%;
    font-weight: 300;
  }
  &__logo{
    width: 18em;
    max-width: 100%;
    margin-top: 1.5em;
  }
  &__groups{
    margin-top: 2.5em;
    @include grid100gap(2.5em);
  }
}


@media screen and (max-width: 992px){
  .contacts{
    padding-top: 2.75em;
    &__grid{
      grid-template-columns: repeat(2,calc(50% - .625em));
      gap: 2.5em 1.25em;
    }
  }

  .contacts-form{
    padding: 2em 1em 2.875em;
    border-radius: .3125em;
    background-color: #EBEBEB;
    &__text {
      gap: .75em;
    }
    &__desc,
    &__title{
      font-size: 1em;
      line-height: 140%;
    }
    &__fields{
      margin-top: 1em;
      gap: 1em;
    }
    &__btn{
      margin: 2em 0 0 auto;
      width: 9.25em;
      span{
        font-size: 1em;
      }
    }
  }

  .contacts-info{
    &__text{
      font-size: 1em;
      line-height: 150%;
      br{
        display: none;
      }
    }
    &__logo{
      width: 13.75em;
      margin-top: 1em;
    }
    &__groups{
      margin-top: 1.5em;
      gap: 1.5em;
      br{
        display: unset;
      }
    }
  }
}


@media screen and (max-width: 576px){
  .contacts{
    .container{
      padding: 0 .75em;
    }
    &__grid{
      grid-template-columns: 100%;
    }
  }
}