.product-detail{
  padding: 3.625em 0 4.875em;
  .m-tabs{
    display: grid;
    grid-template-columns: 50% 50%;
  }
  &__tab{
    height: 2.75em;
    @include center;
    color: $gray2;
    border: 1px solid transparent;
    border-bottom: none;
    border-radius: .3125em .3125em 0 0;
    cursor: pointer;
    transition: all .3s;
    &:hover{
      color: $black;
    }
    &._active{
      pointer-events: none;
      font-weight: 700;
      color: $black;
      border-color: $gray2;
    }
  }
}

.news2{
  padding: 0 0 6em;
}

.use-table,
.use-table2,
.oem-table{
  border: $border;
  border-radius: 0 0 .3125em .3125em;
  padding: 1em 1.875em;
  table{
    width: 100%;
  }
  td{
    border-bottom: $border;
    height: 3.75em;
  }
  tr:last-child{
    td{
      border-bottom: none;
    }
  }
  &__td{
    font-size: 1.125em;
    line-height: 120%;
  }
}

.oem-table{
  td{
    width: 50%;
  }
}

.use-table{
  font-weight: 500;
  td{
    text-align: left;
    &:first-child{

    }
    &:nth-child(1),
    &:nth-child(2){
      width: 25%;
    }
    &:nth-child(3){
      width: 50%;
    }
  }
}

.use-item{
  border-bottom: 1px solid #CCC9C9;
  &:last-child{
    border-bottom: none;
  }
  &._open{
    .use-item__top:before{
      transform: rotate(-180deg);
    }
  }
  &__top{
    display: flex;
    align-items: center;
    padding: 1.25em .875em;
    cursor: pointer;
    &:before{
      content: '';
      background-image: url("data:image/svg+xml;charset=utf8,%3Csvg width='23' height='14' viewBox='0 0 23 14' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M1 1L11.4236 12L22 1.36559' stroke='%23929292' stroke-width='1.86' stroke-miterlimit='10'/%3E%3C/svg%3E");
      width: 1.4375em;
      height: .875em;
      @include bgImgSize(100%);
      margin-right: .875em;
      transition: all .3s;
    }
  }
  &__bottom{
    display: none;
  }
  &__title{
    font-size: 1.125em;
    line-height: 111%;
    font-weight: 700;
  }
  th{
    border-bottom: 1px solid #CCC9C9;
    padding-bottom: .5em;
    font-weight: 400;
    color: #929292;
    span{
      font-size: .875em;
      line-height: 143%;
    }
  }
  td{
    border-color: #CCC9C9;
    font-weight: 500;
  }
  th,td{
    &:first-child{
      padding-left: 2.3125em;
      width: 20em;
    }
  }
}


@media screen and (max-width: 992px){
  .product-detail{
    padding: 2.44em 0 6.44em;
    &__tab{
      height: 2.5em;
      span{
        font-size: .875em;
      }
    }
  }

  .use-table2,
  .use-table,
  .oem-table{
    border-radius: 0;
    padding: .5em 1em;
    td{
      height: 2.5em;
    }
    &__td{
      font-size: .875em;
    }
  }

  .use-item{
    &__top{
      padding: .625em .375em;
      &:before{
        width: 1em;
        height: .625em;
        margin-right: .75em;
      }
    }
    &__title{
      font-size: .875em;
      line-height: 143%;
    }
    th{
      span{
        font-size: .625em;
      }
    }
    th,td{
      &:first-child{
        padding-left: 0;
        width: 7em;
      }
    }
    .use-table__td{
      font-size: .75em;
    }
  }
}


@media screen and (max-width: 576px){
  .use-table{
    width: calc(100% + 3.75em);
    margin-left: -1.875em;
    overflow-x: auto;
    table{
      min-width: 35em;
    }
  }
}
