.info{
  padding: 4.25em 0 7.5em;
  background: #f5f5f5;
  &__logo{
    width: 18.625em;
    margin-left: auto;
    margin-bottom: 5.25em;
    img{
      width: 100%;
      display: block;
    }
  }
}

.news{
  padding: 7em 0 10em;
}

.news-slider{
  margin-top: 3em;
  .swiper{
    &-container{
      width: calc(100% + 1.875em);
    }
    &-slide{
      width: 25%;
      padding-right: 1.875em;
    }
    &-button{
      top: 7.25em;
      &-prev{
        left: -3em;
      }
      &-next{
        right: -3em;
      }
    }
  }
}

.news-card{
  text-align: center;
  &__img{
    border-radius: .3125em;
    @include imgCover(80.3%);
    margin-bottom: 2em;
  }
}


@media screen and (max-width: 992px){
  .info{
    padding: 2em 0 4em;
    &__logo{
      width: 15em;
      margin: 0 auto 2em;
    }
  }

  .news{
    padding: 3em 0 6.25em;
  }

  .news-slider{
    margin-top: 1.25em;
    .swiper{
      &-container{
        width: calc(100% + 1.25em);
      }
      &-slide{
        width: calc(100% / 3);
        padding-right: 1.25em;
      }
      &-button{
        top: 6em;
        &-prev{
          left: -1.5em;
        }
        &-next{
          right: -1.5em;
        }
      }
    }
  }

  .news-card{
    &__img{
      margin-bottom: 1.25em;
    }
    &__title{
      font-size: .875em;
      line-height: 114%;
    }
  }
}


@media screen and (max-width: 576px){
  .news-slider{
    .swiper{
      &-slide{
        width: 50%;
      }
      &-button{
        top: 4em;
      }
    }
  }
}