.catalog-item{
  padding-top: 3.31em;
  &__grid{
    display: grid;
    grid-template-columns: 38.125em calc(100% - 40em);
    gap: 1.875em;
  }
  &__img{
    border: 1px solid #CCC9C9;
    display: block;
    img{
      width: 100%;
      display: block;
    }
  }
  &__subtitle{
    height: 3.375em;
    display: flex;
    align-items: center;
  }
  .props{
    margin-top: .75em;
  }
  &__nav{
    margin-top: 2em;
  }
  &__btn{
    width: 18.3125em;
    font-weight: 500;
    span{
      font-size: 1.5em;
    }
  }
  .text{
    margin-top: 1em;
  }
}

.gallery-slider{
  overflow: hidden;
  border: 1px solid #CCC9C9;
  .swiper{
    &-container{
      width: calc(100% + 1em);
    }
    &-wrapper{
      align-items: stretch;
    }
    &-slide{
      padding-right: 1em;
      height: auto;
    }
    &-button{
      &-prev{
        left: .5em;
      }
      &-next{
        right: .5em;
      }
      &.swiper-button-disabled{
        opacity: 0;
      }
    }
  }
  .catalog-item__img{
    border: none;
  }
}

.catalog-item2{
  .gallery-slider,
  .catalog-item__img{
    border: none;
  }
}

.props-group{
  margin-top: 1em;
  padding-top: 1.125em;
  border-top: 1px solid #CCC9C9;
  @include grid100gap(.75em);
  &:first-child{
    margin-top: 0;
  }
}

.prop-item{
  display: grid;
  grid-template-columns: 19em calc(100% - 20em);
  gap: 1em;
  &__title,
  &__desc{
    font-size: 1em;
    line-height: 150%;
  }
  &__title{
    font-weight: 500;
  }
  &__desc{
    font-weight: 300;
  }
}

.same-products{
  margin-top: 2.25em;
  &__inner{
    border-radius: .3125em;
    border: $border;
    padding: 1em 3.5em 1.75em;
    display: grid;
    grid-template-columns: 11em 23.25em 23.25em;
    gap: 0 5.5em;
  }
  @media screen and (min-width: 993px){
    &--lonely .same-products__inner{
      grid-template-columns: 23.25em 23.25em;
    }
  }
}

.same-group{
  &__title{
    font-size: 1.125em;
    line-height: 177%;
    font-weight: 500;
  }
}

.same-slider{
  margin-top: .625em;
  padding-right: 2.75em;
  position: relative;
  .slider__wrap{
    overflow: hidden;
  }
  .swiper{
    &-container{
      width: calc(100% + 1.625em);
    }
    &-slide{
      padding-right: 1.625em;
      width: 7.0625em;
    }
    &-button{
      top: .75em;
      width: 1.3125em;
      transform: unset;
      svg{
        width: 100%;
      }
      &.swiper-button-disabled{
        opacity: 0;
      }
      &-next{
        right: 1.5em;
      }
    }
  }
}

.product-card{
  display: block;
  text-align: center;
  &__img{
    @include imgCover(70%);
    border: $border;
    margin-bottom: .6875em;
  }
  &__title{
    font-size: 1.125em;
    line-height: 133%;
    color: $primary;
    font-weight: 600;
  }
}


@media screen and (max-width: 992px){
  .catalog-item{
    padding-top: 1.875em;
    &__grid{
      grid-template-columns: repeat(2,calc(50% - .5em));
      gap: 1em;
    }
    &__subtitle{
      height: unset;
      margin-top: .8125em;
    }
    .props{
      margin-top: 1em;
    }
    &__nav{
      justify-content: center;
      margin-top: .75em;
    }
    &__btn{
      height: 2.3125em;
      width: 9.625em;
      padding: 0;
      span{
        font-size: 1em;
      }
    }
  }

  .props-group{
    margin-top: 0;
    padding: 0;
    border-top: none;
    gap: 0;
  }

  .prop-item{
    padding: .5em 0;
    border-top: 1px solid #CCC9C9;
    grid-template-columns: repeat(2,calc(50% - .5em));
    &__title,
    &__desc{
      font-size: .875em;
      line-height: 120%;
    }
  }

  .same-products{
    margin-top: 1.5em;
    &__inner{
      grid-template-columns: 100%;
      padding: 0;
    }
  }

  .same-group{
    padding: .75em 2em;
    border-bottom: $border;
    &:last-child{
      border-bottom: none;
    }
  }

  .same-slider{
    padding-right: 2em;
    width: 100%;
    max-width: 25.5em;
    .swiper{
      &-container{
        width: calc(100% + 1.25em);
      }
      &-slide{
        width: calc(100% / 3);
        padding-right: 1.25em;
      }
      &-button{
        top: 1.25em;
        &-next{
          right: 0;
        }
        &-prev{
          left: -1.75em;
        }
      }
    }
  }
}


@media screen and (max-width: 576px){
  .catalog-item{
    &__grid{
      grid-template-columns: 100%;
    }
    &__top{
      margin-bottom: 1em;
    }
  }

  .same-products{
    .container{
      padding: 0 1em;
    }
  }

  .same-group{
    &__title{
      font-size: .875em;
    }
  }

  .same-slider{
    margin-top: .375em;
    max-width: 19.5em;
    .swiper-button{
      top: .5em;
    }
  }

  .product-card{
    &__img{
      margin-bottom: .5em;
    }
    &__title{
      font-size: .875em;
      line-height: 128%;
    }
  }
}