.search-page{
  padding: 4em 0;
  &__groups{
    margin-top: 4.25em;
    @include grid100gap(4.25em);
  }
}

.search-filter{
  padding: 0 4.75em;
  &__top{
    img{
      width: 2.5em;
      margin-right: 1.25em;
    }
  }
  &__title{
    font-size: 1.25em;
    font-weight: 600;
    color: $blue;
  }
  &__wrap{
    display: grid;
    grid-template-columns: calc(100% - 19.5em) 18.375em;
    justify-content: space-between;
    margin-top: 1.25em;
  }
  &__fields{
    display: grid;
    grid-template-columns: 12.75em 8.125em auto 12.5em;
    gap: 1.125em;
  }
  @media screen and (min-width: 993px){
    .m-select{
      .nice-select{
        height: 2.5em;
        .current{
          font-size: 1em;
        }
      }
      select{
        font-size: 1em;
        height: 2.5em;
        background-size: 1em auto;
        background-position: right 1em center;
      }
    }
  }
  &__nav{
    display: grid;
    grid-template-columns: repeat(2,calc(50% - .5625em));
    gap: 1.125em;
  }
  &__btn{
    width: 100%;
    height: 2.5em;
  }
}

.search-group{
  &__top{
    border-bottom: $border;
    padding-bottom: 1.25em;
    text-align: center;
  }
}

.search-slider{
  margin-top: 3.25em;
  .swiper{
    &-container{
      width: calc(100% + 1.875em);
    }
    &-slide{
      padding-right: 1.875em;
      width: 25%;
    }
    &-button{
      top: 6.75em;
      &-prev{
        left: -2.75em;
      }
      &-next{
        right: -2.75em;
      }
      &.swiper-button-disabled{
        opacity: 0;
        pointer-events: none;
      }
    }
  }
  .product-card{
    &__img{
      padding-bottom: 74%;
      background-color: #F5F5F5;
      border: none;
      margin-bottom: 1em;
    }
    &__title{
      font-size: 1.375em;
    }
  }
}

.place{
  padding-top: 4em;
}

.place-card{
  font-family: $font1;
  font-size: 1em;
  &__title,
  &__desc{
    font-size: 1em;
    line-height: 125%;
  }
  &__title{
    font-weight: 600;
  }
  &__desc{
    margin-top: .3125em;
  }
  &__nav{
    margin-top: .5em;
    color: $gray2;
    a{
      font-size: .75em;
      line-height: 167%;
    }
  }
}

.map{
  height: 42em;
  .ymaps-2-1-79-balloon{
    border-radius: .3125em;
    box-shadow: 2px 2px 5px rgba(0,0,0,0.25);
    transform: translate(65%,2em);
    &__content{
      font-size: 1em;
    }
    &__tail{
      &:after{
        display: none;
      }
    }
  }
}



@media screen and (max-width: 992px){
  .search-page{
    padding: 2.25em 0;
    &__groups{
      margin-top: 2.625em;
      gap: 2.125em;
    }
  }

  .search-filter{
    padding: 0;
    &__top{
      img{
        width: 1.4375em;
        margin-right: .5em;
      }
    }
    &__title{
      font-size: .875em;
    }
    &__wrap{
      grid-template-columns: 100%;
      gap: .9375em;
    }
    &__fields{
      grid-template-columns: 12.5em 8em calc(100% - 33em - .625em) 10.5em;
      gap: .875em;
    }
    &__nav{
      display: grid;
      grid-template-columns: repeat(2,calc(50% - .9375em / 2));
      gap: .9375em;
    }
    &__btn{
      height: 2.0625em;
    }
  }

  .search-group{
    &__top{
      border-bottom: none;
      padding-bottom: 0;
    }
  }

  .search-slider{
    margin-top: .875em;
    .swiper{
      &-container{
        width: calc(100% + 1.25em);
      }
      &-slide{
        padding-right: 1.25em;
        width: calc(100% / 3);
      }
      &-button{
        width: .9375em;
        top: 5.25em;
        &-prev{
          left: -1.5em;
        }
        &-next{
          right: -1.5em;
        }
      }
    }
    .product-card{
      &__img{
        margin-bottom: .25em;
      }
      &__title{
        font-size: 1em;
      }
    }
  }

  .map{
    height: 35em;
  }
}


@media screen and (max-width: 576px){
  .search-filter{
    &__fields{
      grid-template-columns: 100%;
    }
  }

  .search-slider {
    .swiper {
      &-slide {
        width: 50%;
      }
      &-button {
        top: 3.5em;
      }
    }
  }
}