@import "../vars";
.m-btn {
    display: flex;
    align-items: center;
    justify-content: center;
    transition: all ease-out .225s;
    padding: 0 2.25em;
    height: 2.625em;
    border-radius: .3125em;
    font-weight: 700;
    &:focus {
        box-shadow: none;
    }
    &-small{
        height: 1.8125em;
    }
}

.m-btn-red{
    color: $white;
    background-color: $primary;
    &:hover{
        color: $white;
        background-color: darken($primary,10%);
    }
}

.m-btn-gray{
    color: $white;
    background-color: $gray2;
    &:hover{
        color: $white;
        background-color: darken($gray2,10%);
    }
}

.m-btn-blue{
    color: $white;
    background-color: $blue;
    &:hover{
        color: $white;
        background-color: darken($blue,10%);
    }
}