.result {
  padding: 3.75em 0 9.375em;
  &__cards {
    display: grid;
    grid-template-columns: 100%;
  }
}

.result-card {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1.25em 0;
  &:not(:last-child) {
    border-bottom: 0.0625em solid #CCC9C9;
  }
  &__left {
    display: flex;
    align-items: center;
    gap: 3.75em;
  }
  &__model {
    color: #E81D3E;
  }
  &__img {
    width: 7.0625em;
    height: 7.0625em;
    @include imgCover(100%);
    padding-bottom: 0;
    flex-shrink: 0;
    img {
      width: 100%;
    }
  }
  &__btn {
    padding: 0 3.75em;
  }
  @media screen and (min-width: 993px){
    &__left{
      gap: 0;
    }
    &__model {
      width: 8em;
      flex-shrink: 0;
    }
    &__img{
      margin-right: 3.75em;
    }
  }
}

@media screen and (max-width: 992px) {
  .result {
    padding: 2.125em 0 7.5em;
  }

  .result-card {
    padding: 1em 0;
    &__left {
      position: relative;
      flex-direction: column;
      align-items: start;
      gap: 0.625em;
      padding-left: 4.6875em;
    }
    &__model,
    &__title {
      font-size: 0.875em;
    }
    &__img {
      position: absolute;
      top: 50%;
      left: 0;
      transform: translate(0, -50%);
      width: 3.125em;
      height: 3.125em;
    }
    &__btn {
      display: none;
    }
  }
}