.about-main {
  padding: 4em 0 2.8125em;
  &__inner {
    img {
      width: 100%;
      display: block;
      // min-height: 15em;
      margin-bottom: 1.25em;
    }
    p {
      font-size: 1.25em;
      line-height: 150%;
      &:not(:last-child) {
        margin-bottom: 1.25em;
      }
      strong {
        font-weight: 600;
      }
    }
  }
}

.about-info {
  padding: 2.8125em 0 3.5em;
  &__inner {
    position: relative;
    background: #F5F5F5;
    border-radius: 0.3125em;
    padding: 1.25em 2.5em 1.875em;
  }
  &__group {
    position: relative;
    z-index: 2;
    display: flex;
    align-items: start;
    gap: 5em;
    margin-top: 1.875em;
  }
  &__logo {
    max-width: 10em;
    flex-shrink: 0;
    img {
      width: 100%;
    }
  }
  &__group-text {
    max-width: 43.125em;
    display: grid;
    grid-template-columns: 100%;
    gap: 0.3125em;
    p {
      strong {
        display: block;
      }
    }
  }
  .m-h2 {
    position: relative;
    z-index: 2;
  }
  &__items {
    display: grid;
    grid-template-columns: repeat(2, calc(50% - 3.75em / 2));
    align-items: start;
    gap: 2.5em 3.75em;
    margin-top: 3.125em;
  }
  &__item {
    &:nth-child(1) {
      grid-row: 1/3;
    }
  }
  &__list {
    display: grid;
    grid-template-columns: 100%;
    gap: 0.3125em;
    padding-left: 0.625em;
    margin-top: 0.3125em;
    li {
      position: relative;
      padding-left: 1em;
      &:after {
        content: '';
        position: absolute;
        top: 0.5em;
        left: 0;
        width: 0.1875em;
        height: 0.1875em;
        background: #000;
        border-radius: 50%;
      }
    }
  }
  &__icon {
    position: absolute;
    top: 0.625em;
    right: 3.5em;
    max-width: 11.875em;
    img {
      max-width: 100%;
    }
  }
}

.about-info2 {
  padding-bottom: 0 !important;
}


@media screen and (max-width: 992px) {
  .about-main {
    padding: 2em 0 1em;
    &__inner {
      p {
        font-size: 1em;
      }
    }
  }

  .about-info {
    padding: 1em 0;
    .container {
      padding: 0 0.875em;
    }
    &__inner {
      padding: 1.125em 1.125em 1.875em;
    }
    &__text {
      max-width: 9.375em;
      p {
        font-size: 1.125em;
      }
    }
    &__group {
      gap: 1.75em;
      flex-direction: column;
      margin-top: 2.5em;
      .m-h2 {
        font-size: 1em;
      }
    }
    &__group-text {
      gap: 1em;
    }
    &__logo {
      max-width: 6.6875em;
      margin: 0 auto;
    }
    &__items {
      grid-template-columns: 100%;
      gap: 1em;
      margin-top: 2em;
    }
    .text20 {
      font-size: 1em;
    }

    &__icon {
      right: 1.5em;
      max-width: 5em;
    }
  }
}