.footer{
  padding: 3.375em 0 13.25em;
  background-color: #EBEBEB;
  background-image: url(../img/footer-bg.svg);
  background-repeat: no-repeat;
  background-size: auto 100%;
  background-position: right top;
  &__logo{
    display: block;
    width: 10.875em;
    img{
      width: 100%;
      display: block;
    }
  }
  &__inner{
    margin-top: 4.5em;
    padding-left: 3.125em;
  }
  &__links{
    font-weight: 600;
    ul{
      @include grid100gap(.75em);
    }
    a{
      font-size: 1em;
      line-height: 150%;
    }
  }
  &__bottom{
    margin-top: 4.375em;
  }
  &__c{
    margin-top: .875em;
    img{
      width: 1.1875em;
      margin-right: .3125em;
    }
  }
}


@media screen and (max-width: 992px){
  .footer{
    padding: 4em 0 10em;
    background-image: unset;
    .container{
      padding-left: 5.625em;
    }
    &__logo{
      width: 10.625em;
    }
    &__inner{
      margin-top: 5em;
      padding-left: 0;
    }
    &__bottom{
      margin-top: 5.375em;
    }
    &__c{
      margin-top: .6875em;
    }
  }
}


