.m-select{
  select{
    width: 100%;
    height: 2.75em;
    font-size: .75em;
    color: #929292;
    border-radius: 0.41em;
    border: 1px solid #0369AE;
    background-color: $white;
    padding-left: .8em;
    font-family: $font1;
    background-image: url(../img/select-arrow.svg);
    background-size: 1.25em auto;
    background-position: right .83em center;
    background-repeat: no-repeat;
    @include removeSelectArrow;
    &:focus{
      color: $black;
    }
  }
  .nice-select{
    font-size: 1em;
    display: flex;
    align-items: center;
    justify-content: space-between;
    white-space: nowrap;
    height: 2.0625em;
    width: 100%;
    padding: 0 .625em 0 .5em;
    color: #929292;
    border-radius: 0.3125em;
    border: 1px solid #0369AE;
    &.disabled{
      pointer-events: none;
      background: #F6F6F6 !important;
      .current{
        opacity: 0.4;
      }
    }
    &:before{
      content: '';
      order: 3;
      flex-shrink: 0;
      width: .9375em;
      height: .93775em;
      background-image: url(../img/select-arrow.svg);
      @include bgImgSize(100%);
      margin-left: .25em;
      z-index: 5;
    }
    &.open{
      border-color: transparent;
      z-index: 10;
    }
    .current{
      font-size: .75em;
      line-height: 120%;
      position: relative;
      z-index: 6;
    }
    .list{
      border: 1px solid #0369AE;
      top: 0;
      left: -1px;
      margin-top: -1px;
      width: calc(100% + 2px);
      padding: 1.875em .25em .25em;
      border-radius: .3125em;
      background: #F5F5F5;
      box-shadow: none;
      overflow-y: auto;
      max-height: 17em;
      z-index: 5;
      @include grid100gap(.125em);
    }
    .option{
      font-size: .75em;
      line-height: 130%;
      border-radius: 0.41em;
      padding: .25em .75em;
      display: flex;
      align-items: center;
      color: #2B2B2B;
      border: none;
      &:last-child{
        border-bottom: none;
      }
      &.disabled{
        display: none;
      }
      &:hover,
      &.focus,
      &.selected.focus{
        background-color: $blue;
        border-color: $blue;
        font-weight: 400;
        color: #fff;
      }
    }
  }
  &._is-selected{
    .nice-select{
      color: $black;
    }
  }
}

.fg {
  position: relative;
  margin: 0;
  input,
  textarea{
    font-weight: 300;
    border-radius: 0;
    border: none;
    border-bottom: 1px solid $black;
    padding: 0;
    height: 3.25em;
    width: 100%;
    font-size: 1.125em;
    background-color: transparent;
    color: $black;
    &::placeholder {
      color: $black;
    }
    &:focus {
      border-color: $blue;
    }
  }
  textarea{
    padding-top: 1.19em;
    padding-bottom: 1.19em;
    height: 13.0625em;
    resize: vertical;
  }
  &__field{
    position: relative;
  }
  .invalid-feedback{
    position: absolute;
    top: 100%;
    left: 0;
    width: 100%;
  }
  &._error{
    input,
    textarea{
      border-color: $primary;
      color: $primary;
      &::placeholder{
        color: $primary;
      }
    }
    .fg__error-text{
      display: block;
    }
  }
  &__error-text{
    margin-top: .375em;
    color: $primary;
    display: none;
  }
}



@media screen and (max-width: 992px){
  .fg{
    input,
    textarea{
      font-size: 1em;
    }
  }
}


