.error404{
  padding: 4.75em 0;
  &__inner{
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  &__img{
    width: 51.25em;
    margin-top: 4em;
    transform: translateX(-10%);
  }
  &__btn{
    margin-top: 6.5em;
    height: 2.625em;
    span{
      font-size: 1.25em;
    }
  }
}


@media screen and (max-width: 992px){
  .error404{
    padding: 3.75em 0;
    &__img{
      width: 16.75em;
      margin-top: 3em;
    }
    &__btn{
      margin-top: 2.875em;
      height: 2.3125em;
      span{
        font-size: 1em;
      }
    }
  }
}


