.slider{
  position: relative;
  .swiper{
    &-wrapper{
      align-items: stretch;
    }
    &-slide{
      height: auto;
    }
    &-button{
      margin: 0;
      transform: translateY(-50%);
      &-prev{
        left: -1.75em;
      }
      &-next{
        right: -1.75em;
      }
    }
  }
  &__wrap{
    overflow: hidden;
  }
}

.swiper-button{
  width: 1.6875em;
  height: auto;
  @include center;
  svg{
    width: 100%;
  }
  &:hover{
    path{
      stroke: $primary;
    }
  }
}

.swiper-pagination{
  position: static;
  margin-top: 1.5em;
  @include center;
  &-bullet{
    width: .5em;
    height: .5em;
    opacity: 1;
    background-color: $gray;
    margin: 0 .25em !important;
    &-active{
      background-color: darken($gray,20%);
    }
  }
}

.main{
  padding: 5.75em 0 3.5em;
}

.main-slider{
  overflow: hidden;
}

.main-banner{
  display: block;
  &__img{
    width: 100%;
    display: block;
    min-height: 100%;
    object-fit: cover;
  }
  ._view2{
    display: none;
  }
}

.categories{
  padding: 3.5em 0 5.875em;
}

.categories-slider{
  margin-top: 2.5em;
  .swiper{
    &-slide{
      width: 25%;
    }
  }
}

.category-card{
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  &__img{
    width: 13.5em;
    height: 13.5em;
    background-color: $gray;
    border-radius: 50%;
    position: relative;
    margin-bottom: 1.125em;
    img{
      @include centerAbsolute;
      max-width: calc(100% + 3em);
      max-height: calc(100% + 3em);
    }
  }
  &__title{
    position: relative;
    z-index: 2;
  }
}


@media screen and (max-width: 992px){
  .slider{
    .swiper{
      &-button{
        &-prev{
          left: -1.5em;
        }
        &-next{
          right: -1.5em;
        }
      }
    }
  }

  .main{
    padding: 3em 0 1.875em;
  }

  .swiper-button{
    width: 1em;
  }

  .categories{
    padding: 1.875em 0 3.5em;
  }

  .categories-slider{
    margin-top: 1.75em;
    .swiper-slide{
      width: calc(100% / 3);
    }
  }

  .category-card{
    padding: 0 .5em;
    &__img{
      width: 7.5em;
      height: 7.5em;
      margin-bottom: .625em;
      img{
        max-width: calc(100% + 1.5em);
        max-height: calc(100% + 1.5em);
      }
    }
    &__title{
      font-size: 1em;
      font-weight: 500;
    }
  }
}


@media screen and (max-width: 576px){
  .main{
    .container{
      padding: 0;
    }
  }

  .swiper-pagination{
    margin-top: .5em;
  }

  .main-banner{
    ._view1{
      display: none;
    }
    ._view2{
      display: block;
    }
  }

  .categories-slider{
    .swiper-slide{
      width: 50%;
      transition: opacity .3s;
      &-prev{
        opacity: 0;
        pointer-events: none;
      }
    }
  }
}
