.advantages{
  padding-top: 4.5em;
  &__grid{
    display: grid;
    grid-template-columns: repeat(4,calc(25% - 5.625em / 4));
    gap: 1.875em;
  }
}

.advantage-item{
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  &__icon{
    @include center;
    height: 7.125em;
    img{
      max-height: 100%;
    }
  }
  &__text{
    margin-top: 2em;
    @include grid100gap(.625em);
  }
  &__title{
    font-size: 1.25em;
    line-height: 120%;
    font-weight: 600;
  }
  &__desc{
    font-size: 1.125em;
    line-height: 120%;
  }
}

.conf{
  padding: 4em 0;
  overflow: hidden;
  .text{
    margin-top: .75em;
  }
}


@media screen and (max-width: 992px){
  .advantages{
    padding-top: 2em;
    &__grid{
      grid-template-columns: repeat(4,calc(25% - 3em / 4));
      gap: 1em;
    }
  }

  .advantage-item{
    &__icon{
      height: 5.125em;
    }
    &__text{
      margin-top: .5em;
      @include grid100gap(.625em);
    }
    &__desc,
    &__title{
      font-size: 1em;
      line-height: 125%;
    }
  }

  .conf{
    padding: 2.5em 0;
    .text{
      margin-top: 1.25em;
    }
  }
}


@media screen and (max-width: 576px){
  .advantages{
    padding-top: 2em;
    &__grid{
      grid-template-columns: repeat(2,calc(50% - .625em));
      gap: 2.75em 1.25em;
    }
  }

  .conf{
    .m-h1{
      font-size: 1.125em;
    }
  }
}