.modal{
    .btn-close{
        position: absolute;
        top: 1em;
        right: 1em;
    }
}

.modal-dialog{
    max-width: 34.875em;
    border: none;
}

.modal-content{
    padding: 5.625em 4em;
    border: none;
    box-shadow: .3125em .3125em .3125em rgba($black,.25);
}

.thank-modal{
    &__inner{
        display: flex;
        flex-direction: column;
        align-items: center;
        text-align: center;
    }
    &__icon{
        margin: 0 0 1em;
    }
}


