.text{
  line-height: 150%;
  &._lh130{
    p,li{
      line-height: 130%;
    }
    p{
      @include lastM(0 0 1.25em);
    }
  }
  td,
  p,li{
    font-size: 1.25em;
    line-height: 150%;
  }
  li{
    line-height: 120%;
  }
  ol,
  ul,
  p{
    @include lastM(0 0 1.5em);
  }
  ol {
    counter-reset: list;
    li {
      counter-increment: list;
      &:before {
        content: counter(list) '. ';
      }
    }
  }
  ul{
    @include grid100gap(.75em);
    font-weight: 500;
    li{
      position: relative;
      padding-left: 1.35em;
      &:before{
        content: '';
        position: absolute;
        top: .2em;
        left: 0;
        width: .75em;
        height: .75em;
        background-color: $primary;
      }
    }
  }
  table{
    width: 100%;
    border-collapse: collapse;
    margin: -1em 0 2.25em;
    &:last-child{
      margin-bottom: 0;
    }
    &:first-child{
      margin-top: 0;
    }
  }
  td{
    border: $border;
    min-height: 4.25em;
    padding: .25em 1.25em;
  }
}



@media screen and (max-width: 992px){
  .text{
    line-height: 137%;
    p{
      font-size: 1em;
      line-height: 137%;
      @include lastM(0 0 1.25em);
    }
    td,
    li{
      font-size: 1em;
      line-height: 125%;
    }
    ul{
      li{
        padding-left: .875em;
        &:before{
          width: .5em;
          height: .5em;
          top: .375em;
        }
      }
    }
    table{
      margin-top: -.5em;
      margin-bottom: 1.25em;
    }
    td{
      padding: 1.25em .875em;
    }
  }
}


